import React, { useState } from 'react';
import Mask from '../index';

const Demo = () => {
    const [open, set] = useState(false);

    const handleClick = () => {
        set(true);
        setTimeout(() => set(false), 3000);
    }

    return (
        <>
            <button onClick={handleClick}></button>
            <Mask open={open}/>
        </>
    );
}

export default Demo;